@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

body {
	margin: 0;
	padding: 0;
	font-family: 'Poppins', sans-serif;
	overflow-x: auto; /* Enable horizontal scrolling */
}

#otpless-login-page {
	box-shadow: none !important;
	border: 0px !important;
	border-radius: 0% !important;
}

/* Define the style for the horizontal scrollbar */
.scroll-bar::-webkit-scrollbar {
	height: 4px; /* Set the height of the horizontal scrollbar */
}

/* Define the style for the horizontal scrollbar thumb (the draggable part) */
.scroll-bar::-webkit-scrollbar-thumb {
	background-color: rgb(148, 135, 135); /* Set the color of the horizontal scrollbar thumb */
	border-radius: 2px; /* Set the border radius */
}

/* Define the style for the horizontal scrollbar track when being hovered over */
.scroll-bar::-webkit-scrollbar-track:hover {
	background-color: rgb(241, 236, 236); /* Set the color of the horizontal scrollbar track on hover */
}
